import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
// import { TabsPage } from "../tabs/tabs";
import { PatientIndexPage } from '../patient-index/patient-index';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
    
  }
  logIn(username: HTMLInputElement, identity: HTMLInputElement, phone: HTMLInputElement, medicalid: HTMLInputElement) {
    if (username.value.length == 0) {
        alert("请输入姓名");
    } else if (identity.value.length == 0) {
        alert("请输入身份证号");
    } else if (phone.value.length == 0) {
        alert("请输入手机号");
    } else if (medicalid.value.length == 0) {
        alert("请输入就诊卡号");
    }else {
        // let userinfo: string = '用户名：' + username.value + '密码：' + password.value;
        //alert(userinfo);
        this.navCtrl.push(PatientIndexPage);
    }

}
  

}



